@import url(https://fonts.googleapis.com/css?family=Raleway:100,200,300);

* { 
    margin: 0;
    padding: 0;
}

html, body {
    overflow: hidden;
}

.webgl { 
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.loading-animation-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.progress {
    display: inline-block;
    width: 50%;
    height: .5%;
    margin: 5px;
    border-radius: 20px;
    background: #f9f9f9;
}

.bar {
    height: 100%;
    border-radius: 20px;
    background: #f9f9f9;
}

