label {
    font-family: 'Raleway';
    font-weight: 100;
    color: white;
    font-size: 1em;
}

.footer {
    display:flex;
    flex-direction: row-reverse;
    z-index: 1;
    position: fixed;
    left: -1em;
    bottom: 1em;
    width: 100%;
    color: white;
}